* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.notification {
  width: 250px;
  text-align: center;
  color: beige;
  cursor: pointer;
  background-color: green;
  border-radius: 4%;
  padding: 24px;
}

.notification:hover {
  background-color: #0c2b0c;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

.is-hidden {
  display: none;
}

.ad {
  width: 300px;
  text-align: center;
  background-color: #0ff;
  border-radius: 5%;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

.container {
  display: flex;
}

.box {
  width: 60px;
  height: 60px;
  border-radius: 3%;
  outline: 2px solid #212121;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  margin-right: 8px;
  display: flex;
}

.btn {
  cursor: pointer;
  margin-top: 32px;
  font-size: 32px;
}

